.infoWindow,
.infoWindow_away {
    position: absolute;
    top: 0;
    right: -30%;
    width: 100%;
    max-width: 550px;
    height: 100%;
    background-color: rgba(28, 28, 28, 0.4);
    backdrop-filter: blur(5px);
    animation: slideInFromRight 2s ease;
    animation-fill-mode: forwards;
    z-index: 1000;
}

.infoWindow_away {
    animation: slideOutToRight 2s ease-in;
}

@keyframes slideInFromRight {
    0% {
        right: -100%;
    }

    100% {
        right: 0%;
    }
}

@keyframes slideOutToRight {
    0% {
        right: 0%;
    }

    100% {
        right: -30%;
    }
}

.infoWindow-exit {
    padding: 8px 10px;
    text-align: center;
    font-size: 1em;
    left: 0px;
    border-radius: 0 0 5px 0px;
    position: absolute;
    z-index: 10;
}

.infoWindow-exit:hover {
    background-color: rgba(255, 255, 255, 0.2);
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.8);
    cursor: pointer;
}


.infoWindow-wrapper {
    position: relative;
    height: calc(100% - 40px);
    width: calc(100% - 40px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 20px 0 20px;
}


.infoWindow-techStack {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    padding: 4px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.8);
}

.infoWindow-techStack>img{
    max-width: 80px;
    height: 50px;
    object-fit: contain;
    /* filter: drop-shadow(0px 0px 20px var(--white)); */

}

.infoWindow-techStack-resume-container{
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    margin-top: 10px;
}

.infoWindow-techStack-container{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    flex-wrap: nowrap;
    margin-top: 10px;
}

.infoWindow-techStack-container-img {
    max-width: 80px;
    height: 45px;
    object-fit: contain;
    margin: 4px 5px;
    filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.8));
}


.infoWindow-description {
    padding-top: 25px;
    position: relative;
    font-size: 1.1em;
    text-align: justify;
    float: none;
    font-family: "Helvetica", 'Arial';
    white-space: pre-wrap;
    line-height: 1.5;
}

.about-description, .infoWindow-description{
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.infoWindow-description::-webkit-scrollbar{display: none;}
.about-description::-webkit-scrollbar{display: none;}


.infoWindow-description>svg {
    top: 0px;
    right: 8px;
    height: 25px;
    width: 25px;
}


.infoWindow-description>svg:hover {
    cursor: pointer;
    border-radius: 50rem;
    box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.2);
}


.infoWindow-button {
    position:relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
}


.infoWindow-button>div>svg {
    position: relative !important;
    margin: 0 4px;
}

.infoWindow-button>div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 40px;
    padding: 5px;
    border-radius: 3px;
    cursor: pointer;
}

.infoWindow-button>div:hover {
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.8);
    background-color: rgba(255, 255, 255, 0.2);
}

.contact-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.contact-title {
    position: relative;
    width: 100%;
    top: 40%;
    font-size: 2.5em;
    animation: slideToTop 3s ease-in-out;
    animation-delay: 5s;
    animation-fill-mode: forwards;
}

@keyframes slideToTop {
    0% {
        top: 40%;
        position: relative;
    }

    100% {
        top: 0;
        position: relative;
    }
}

.contact-title__t1 {
    position: relative;
    right: -10%;
    opacity: 0%;
    animation: slideFromRight 4s linear;
    animation-delay: 2s;
    animation-fill-mode: forwards;
}

@keyframes slideFromRight {
    0% {
        right: -10%;
        opacity: 0%;
    }

    22% {
        opacity: 100%;
    }

    44%,
    66%,
    88%,
    100% {
        right: 0;
        opacity: 100%;
    }
}

.contact-title__t2 {
    position: relative;
    opacity: 0%;
    left: -10%;
    animation: slideFromLeft 4s ease-in-out;
    animation-delay: 2s;
    animation-fill-mode: forwards;
}

@keyframes slideFromLeft {
    0% {
        left: 0%;
        opacity: 0%;
    }

    22% {
        opacity: 100%;
    }

    44%,
    66%,
    88%,

    100% {
        left: 10%;
        opacity: 100%;
    }
}

.contact-form {
    opacity: 0%;
    height: 100%;
    margin-top: 20px;
    animation: fadeInFromBack 5s linear;
    animation-delay: 9s;
    animation-fill-mode: forwards;
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    z-index: 1;
    caret-color: var(--white);
}

.contact-form-notification {
    position: absolute;
    background-color: var(--transparentBlack);
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    font-size: 2em;
    z-index: 1;
    border-radius: 5px;
}

.contact-form-notification__exit {
    border-radius: 3px;
    background-color: var(--white);
    padding: 8px;
    margin-top: 6px;
    cursor: pointer;
    color: var(--black);
}

.contact-form>span {
    font-size: 1.2em;
    caret-color: transparent;
}

.contact-form>div>input,
.contact-form>textarea {
    color: var(--white);
    font-size: 1.2em;
    font-family: 'Cinzel', serif;
}

.contact-form>div>input:focus,
.contact-form>textarea:focus {
    outline: none;
    box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.2) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.2);

}

.contact-form-input-container {
    width: 100%;
    height: 45px;
    display: flex;
    margin: 6px 0;
    justify-content: space-between;
}

.contact-form-input,
.contact-form-input__error {
    overflow: hidden;
    white-space: nowrap;
    width: 50%;
    padding: 10px;
    background-color: rgb(255, 255, 255, 0.1);
    border: 1px solid var(--white);
    color: white;
    border-radius: 3px;
}

.contact-form-input:hover,
.contact-form-input__error:hover {
    animation: expandInput 1s linear;
    animation-fill-mode: forwards;
}

.contact-form-input__error {
    border: 1px solid var(--red);
}

@keyframes expandInput {
    0% {
        width: 50%;
    }

    100% {
        width: 80%;
    }
}

.contact-form-textarea {
    height: 100%;
    padding: 7px;
    background-color: rgb(255, 255, 255, 0.1);
    border: 1px solid var(--white);
    border-radius: 3px;
}

.contact-form-send-icon__active,
.contact-form-send-icon__inactive {
    width: 40px !important;
    height: 40px !important;
    bottom: 5px;
    right: 5px;
    cursor: pointer;
    pointer-events: all;
}

.contact-form-send-icon__inactive {
    pointer-events: none;
    color: var(--disabledButton);
}



.contact-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    opacity: 0%;
    margin-top: 15px;
    animation: fadeInFromBack 5s linear;
    animation-delay: 9s;
    animation-fill-mode: forwards;
}

.contact-info__phone,
.contact-info__social {
    display: flex;
    align-items: center;
    font-size: 1.3em;
}

.contact-info__phone>svg,
.contact-info__social>a>svg {
    position: relative !important;
    width: 30px;
    height: 30px;
    margin: 0 6px
}



.contact-info__social>a>svg:hover {
    cursor: pointer;
    box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.2) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.2);
    border-radius: 50%;
}

.contact-reviews {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    opacity: 0%;
    position: relative;
    animation: fadeInFromBack 5s linear;
    animation-delay: 9s;
    animation-fill-mode: forwards;
    overflow: hidden;
}

.contact-reviews-background {
    position: absolute;
    border-radius: 10px;
    background-color: var(--transparentBlack);
    backdrop-filter: blur(5px);
    /* padding: 20px; */
}

.contact-reviews__current {
    position: relative;
    padding: 20px;
    box-shadow: 0px 0px 1px 3px rgba(255, 255, 255, 0.2) inset;
    border-radius: 10px;
    opacity: 0;
    animation: slideInAndOut 20s infinite;
    z-index: 1;
}

.contact-reviews__current>pre {
    text-align: right;
}


@keyframes slideInAndOut {
    0% {
        right: -100%;
        transform: scale(0.7);
        opacity: 1;
    }

    8% {
        right: 0;
        transform: scale(0.7);
        opacity: 1;
    }

    45% {
        right: 0%;
        transform: scale(1);
        opacity: 1;
    }

    50% {
        right: 0%;
        transform: scale(1);
        opacity: 1;
    }

    65% {
        right: 0%;
        transform: scale(1);
        opacity: 1;
    }

    92% {
        right: 0;
        transform: scale(0.7);
        opacity: 1;
    }

    /* 95%{right: 100%; transform:scale(0.7);opacity: 1;} */
    100% {
        right: 100%;
        transform: scale(0.7);
        opacity: 1;
    }
}

.contact-reviews__left-svg {
    transform: scale(-1, 1);
    top: 10px;
    left: 10px;
    width: 50px !important;
    height: 50px !important;
    color: rgba(255, 255, 255, .4);
}

.contact-reviews__right-svg {
    bottom: 10px;
    right: 10px;
    width: 50px !important;
    height: 50px !important;
    color: rgba(255, 255, 255, .4);
}


.about-wrapper {
    position: relative;
    height: calc(100% - 40px);
    width: calc(100% - 40px);
    padding: 20px;
    backdrop-filter: blur(5px);
    overflow: auto;
}

.about-wrapper>svg {
    top: 20px;
    right: 20px
}

.about-title {
    position: relative;
    display: flex;
    justify-content: center;
    height: 40px;
    text-align: center;
    font-size: 2.2em;
    margin-bottom: 10px;
}

.about-image {
    position: absolute;
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 50rem;
    /* float: left;
    margin: 8px 8px 8px 0; */
}

.about-description {
    font-size: 1.1em;
    text-align: justify;
    float: none;
    font-family: "Helvetica", 'Arial';
    white-space: pre-wrap;
    line-height: 1.3;
}



.about-resume {
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.2);
    animation: backgroundFade 3s linear infinite;
    overflow: hidden;
    z-index: 1;
}


@keyframes backgroundFade {
    0% {
        box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.2) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.2);
    }

    50% {
        box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.4) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.4);
    }

    100% {
        box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.2) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.2);
    }
}

.about-resume>svg {
    position: relative !important;
    margin-left: 5px;
}

/* 
.about-resume:hover{
    box-shadow: -1px 0 1px 1px rgba(255, 255, 255), inset -1px 0 0 1px rgba(255, 255, 255);
} */

.about-resume:hover>svg {
    animation: slideToBottom 2s linear;
}

@keyframes slideToBottom {
    0% {top: 0px}
    10% {top: 40px}
    20% { opacity: 0;}
    51% { top: -30px; opacity: 0;}
    75% {top: 0 }
    100% { top: 0 }
}



.carousel {
    position: relative;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    height: 400px;
}

.inner{
    white-space: nowrap;

    transition: transform 0.3s;
}

.carousel-item{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.indicators{
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    left: 0%;
    top: 0%;
    height: 100%;
}

.indicators > button{
    padding: 2px;
    border-radius: 50%;
    border: 0px;
    margin: 5px;
    padding: 4px;
    cursor: pointer;
    background-color: var(--transparentBlack);
}
.indicators > button:hover{
    box-shadow: 0px 0px 5px 4px rgba(255, 255, 255, 0.2);
}

.indicators > button.active{
    background-color: var(--white);
}

.carousel-image {
    width: 100%;
    height: 300px;
    object-fit:contain;
    border-radius: 5px;
    filter: drop-shadow(0px 0px 5px rgba(255, 255, 255, 0.5));
}

.carousel-links {
    position: absolute;
    top: 0px;
    right: 0;
    padding: 3px;
    background-color: rgb(0 0 0 / 0.4);
    border-radius: 3px;
    display: flex;
    z-index: 1;
}

.carousel-links>a {
    position: relative;
    height: 30px;
    width: 30px;
    margin: 0 8px;
}

.carousel-links>a>svg {
    position: relative;
    height: 100%;
    width: 100%;
    filter: drop-shadow(1px 0px 5px rgb(0 0 0 / 0.4));
}

.carousel-links>a>svg:hover {
    cursor: pointer;
    filter: drop-shadow(1px 0px 10px rgb(0 0 0 / 1));
    border-radius: 50%;
}

.carousel-links>a>svg:hover:after {
    position: absolute;
    color: red;
    top: 5px;
    content: ">> Hovering >>";
    /* You can add simple static text in CSS as well. */
    position: inline-block;

}



.loader__element {
    position: relative;
    width: 100%;
    height: 4px;
    bottom: 0px;
    /* 	backrgound: var(--back-color); */
}

.loader__element:before {
    content: '';
    display: block;
    background-color: var(--loader-color);
    height: 4px;
    width: 0;
    animation: getWidth var(--carouseltime) linear infinite;
}

.infoWindow-description {
    overflow: auto;
}

@keyframes getWidth {
    100% {
        width: 100%;
    }
}





@media (min-width:800px) and (max-width: 1200px) and (max-height: 1200px) {

    .infoWindow-techStack,
    .infoWindow-description {
        font-size: 1em;
    }

    .infoWindow-button {
        width: calc(100% - 40px);
    }
}

@media (min-width:600px) and (max-width: 799px) and (min-height: 450px) {
    .contact-title {
        font-size: 2em;
    }

    .contact-reviews {
        font-size: 1em;
    }

    .infoWindow-techStack,
    .infoWindow-description {
        font-size: 1.2em;
    }
    .carousel-image{
        height: 200px;
    }
    .carousel-links>a {
        height: 30px;
        width: 30px;
    }

    .infoWindow-description {
        padding-top: 25px;
    }

    .infoWindow-description>svg {
        height: 25px;
        width: 25px;
    }

    .infoWindow-button {
        width: calc(100% - 40px);
    }
}


