    .contact-form-send-icon__inactive {
        width: 30px !important;
        height: 30px !important;
    }

    .contact-reviews {
        font-size: 0.6em;
    }

    .contact-reviews__current {
        padding: 10px;
    }

    .infoWindow-techStack,
    .infoWindow-description {
        font-size: 0.7em;
    }
    .carousel-image{
        height: 200px;
    }
    .carousel-links>a {
        height: 20px;
        width: 20px;
    }

    .infoWindow-description {
        padding-top: 20px;
    }

    .infoWindow-description>svg {
        height: 20px;
        width: 20px;
    }

    .infoWindow-button {
        position:relative;
        width: 100%;
    }
}

