.home-background {
    height: 100vh;
    width: 100vw;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--white);
    overflow: hidden;
    user-select:none;
    -moz-user-select:none;
    -webkit-user-select:none;
    -webkit-touch-callout:none;
    -ms-user-select:none;
}

.swamp, .swamp-water, .forest-trees, .forest-ground {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-size: cover;
}

.swamp{background-image: url("../../resources/Background/black-forest.jpg");}

.swamp-water{
    background-image: url("../../resources/Background/swamp-water.png");
    filter: url("#turbulence");
    z-index: 2;
}

.forest-trees{
    background-image: url("../../resources/Background/black-forest-trees.png");
    z-index: 3;
}

.forest-ground{
    background-image: url("../../resources/Background/black-forest-ground.png");
    z-index: 1;
}

.swamp-darkness {
    position: absolute;
    height: 100vh;
    width: 100vw;
    animation: day-and-night 56s linear infinite;
    background-color: rgba(28, 28, 28);
    z-index: 3;
}

@keyframes day-and-night {
    0% {
        opacity: 0%;
    }

    25% {
        opacity: 30%;
    }

    50% {
        opacity: 65%;
    }

    75% {
        opacity: 30%;
    }

    100% {
        opacity: 0%;
    }
}

svg {
    position: absolute;
}






.maxWindow {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-color: var(--black);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);

}


.maxWindow-alert {
    position: relative;
    width: 30em;
    height: 15em;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    padding: 20px;
    font-size: 30px;
    z-index: 10;
}

.maxWindow-title {
    position: absolute;
    top: 0;
    font-size: 40px;
    text-align: center;
    padding-bottom: 20px;
    animation: fadeInFromTop 8s ease;
    animation-fill-mode: forwards;
}


.maxWindow-text {
    margin-top: 12%;
    padding-bottom: 20px;
    animation: fadeInFromBack 8s ease;
    animation-delay: 2s;
    animation-fill-mode: forwards;
    opacity: 0%;
}

.maxWindow-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    /* height: 100%; */
    padding: 10px 20px;
    animation: fadeInFromBack 8s ease-in-out;
    animation-delay: 3s;
    animation-fill-mode: forwards;
    opacity: 0%;
    pointer-events: none;
    cursor: none;
}

.maxWindow-button>div {
    padding: 15px 30px;
    box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.2) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.2);
}

.maxWindow-button>div:hover {
    background-color: var(--white);
    color: var(--black);
    cursor: pointer;
}

@keyframes fadeInFromTop {
    0% {
        opacity: 0%;
        top: 0%;
    }

    30%,
    70%,
    90%,
    100% {
        opacity: 100%;
        top: 5%
    }
}

@keyframes fadeInFromBack {
    0% {
        opacity: 0%;
    }

    30%,
    70%,
    90%,
    100% {
        opacity: 100%;
        pointer-events: all;
        cursor: pointer;
    }
}








