.round-fog {
    display: flex;
    width: 100%;
    height: 100%;
    filter: url('#fog-filter');
    position: absolute;
    z-index: 100;
}

.round-fog>div {
    max-width: 350px;
    max-height: 350px;
    width: 100%;
    height: 100%;
    margin: auto;
    border: 10px solid var(--white);
    border-radius: 50rem;
    filter: blur(6px) contrast(160%);
    background-color: transparent;
    overflow: hidden;
}

.firefly {
    filter: blur(1px);
    z-index: 1;
    position: absolute;
    /* animation: day-and-night 56s linear infinite; */
}

.pie-parent {
    position: absolute;
    padding: 0;
    margin: 1em auto;
    width: 20em;
    height: 20em;
    border-radius: 50%;
    overflow: hidden;
    animation: fadeInFromBack 3s ease-in-out;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    z-index: 10;
}

.pie-button {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 50%;
    transform-origin: 0% 100%;
    animation: open-pie 8s cubic-bezier(0.4, 0.0, 0.2, 1), fadeInFromBack 3s ease-in-out;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    opacity: 0%;
    overflow: hidden;
}


.pie-button:hover::after{
    position: absolute;
    content: "";
    height: 160%;
    width: 160%;
    bottom: -50%;
    left: -50%;
    background-color: rgba(255, 255, 255, .15);
    backdrop-filter: blur(5px);
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.8);
    box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.1) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.1);
    border-radius: 40%;
    animation: spin 16s ease-in-out infinite;
}

@keyframes spin {
    0%{transform: translateY(-20%) translateX(20%) rotate(0deg);}
    50%{transform: translateY(-60%) translateX(60%) rotate(400deg)}
    100%{transform: translateY(-20%) translateX(20%) rotate(0deg);}

}

.pie-button-wrapper-text {
    position: absolute;
    left: -100%;
    width: 200%;
    height: 200%;
    padding-top: 60px;
    animation: open-pie-text 8s cubic-bezier(0.4, 0.0, 0.2, 1), fadeInFromBack 3s ease-in-out;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    opacity: 0%;
}

.pie-button-text{
    color: rgb(197, 197, 197);
    text-align: center;
    transform: scale(var(--scale));
}


@keyframes fillUpBackground {
    0%{background-image: radial-gradient(farthest-corner at 0% 100%, transparent 0 100%);}
    100%{background-image: radial-gradient(farthest-corner at 0% 100%, rgba(255, 255, 255, .15) 0 100%);}
}

@keyframes open-pie {
    0% {
        transform: rotate(-75deg) skewY(var(--skewY)) scale(var(--scale));
        pointer-events: none;
        box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.1) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.1);
    }

    30%,
    70%,
    100% {
        transform: rotate(var(--rotX)) skewY(var(--skewY)) scale(var(--scale));
    }
}

@keyframes open-pie-text {
    0% {
        transform: skewY(var(--skewY)) rotate(var(--rotX)) scale(var(--scale)) rotateY(var(--rotY));
        box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.1) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.1);
    }

    30%,
    70%,
    100% {
        transform: skewY(var(--skewY)) rotate(var(--rotX)) scale(var(--scale));
    }
}


