.audioWrapper {
    position: absolute;
    top: 25px;
    left: 25px;
    z-index: 10;
}

.audioWrapper>svg {
    width: 40px;
    height: 40px;
    border-radius: 50rem;
}

.audioWrapper>svg:hover {
    cursor: pointer;
    box-shadow: 0px 0px 30px 10px rgba(255, 255, 255, 0.2) inset, 0px 0px 30px 10px rgba(255, 255, 255, 0.2);
}





.breadcrumbs {
    position: absolute;
    display: flex;
    align-items: center;
    top: 30px;
    left: 85px;
    z-index: 10;
    font-size: 1.5em;
}

.breadcrumbs>div {
    margin: 0 6px;
}

.breadcrumbs>div:hover {
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

.android{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
}
.android-robot{
    position: absolute;
    top: 40%;
    left: 10%;
    max-width: 100px;
}


